export class BasketModel {
    /**
     * @param eventBus - для генерации событий при изменениях корзины
     * @param initial - начальные id (опционально)
     */
    constructor(eventBus, initial = []) {
        this.items = [];
        this.eventBus = eventBus;
        this.items = [...initial];
    }
    has(id) {
        return this.items.includes(id);
    }
    add(id) {
        if (!this.has(id)) {
            this.items.push(id);
            this.eventBus.emit('product:add', { id });
            this.eventBus.emit('basket:changed', { items: this.items.slice() });
        }
    }
    remove(id) {
        const idx = this.items.indexOf(id);
        if (idx >= 0) {
            this.items.splice(idx, 1);
            this.eventBus.emit('product:remove', { id });
            this.eventBus.emit('basket:changed', { items: this.items.slice() });
        }
    }
    clear() {
        this.items = [];
        this.eventBus.emit('basket:changed', { items: [] });
    }
}
