import './scss/styles.scss';
// Точка входа: инициализация API, моделей, EventBus и примера рендера одного компонента
import { Api } from './api/api';
import { ApiClient } from './components/base/ApiClient';
import { EventBus } from './components/base/EventBus';
import { ProductModel } from './components/models/ProductModel';
import { BasketModel } from './components/models/BasketModel';
import { OrderModel } from './components/models/OrderModel';
import { UserModel } from './components/models/UserModel';
import { PageView } from './components/views/PageView';
import { CardView } from './components/views/CardView';
import { BasketView } from './components/views/BasketView';
async function bootstrap() {
    const root = document.getElementById('app');
    if (!root)
        throw new Error('Нет корневого элемента #app');
    // контейнеры
    const catalogContainer = document.createElement('div');
    catalogContainer.id = 'catalog';
    const basketContainer = document.createElement('div');
    basketContainer.id = 'basket';
    root.appendChild(catalogContainer);
    root.appendChild(basketContainer);
    // init infra
    const api = new Api('https://api.example.com'); // <-- замените на реальный URL
    const apiClient = new ApiClient(api);
    const bus = new EventBus();
    // models
    const productModel = new ProductModel(apiClient, bus);
    const basketModel = new BasketModel(bus);
    const orderModel = new OrderModel();
    const userModel = new UserModel();
    // views
    const pageView = new PageView(catalogContainer);
    const basketView = new BasketView(basketContainer);
    // контроллеры/подписки
    // когда продукты загружены — вывести их
    bus.on('products:loaded', ({ items }) => {
        // преобразуем ApiProduct -> ViewProduct
        const viewItems = items.map((p) => {
            var _a;
            return ({
                id: p.id,
                title: p.title,
                image: p.image,
                price: (_a = p.price) !== null && _a !== void 0 ? _a : 0,
                inBasket: basketModel.has(p.id),
            });
        });
        pageView.renderCards((parent, item) => {
            // для каждого товара создаём CardView
            const cv = new CardView(parent, item, item.inBasket, bus);
            cv.render();
        }, viewItems);
    });
    // при изменении корзины — перерендер корзины
    bus.on('basket:changed', ({ items }) => {
        const products = items
            .map((id) => productModel.getProductById(id))
            .filter((p) => p !== undefined);
        // трансформируем в ViewProduct
        const view = products.map((p) => {
            var _a;
            return ({
                id: p.id,
                title: p.title,
                image: p.image,
                price: (_a = p.price) !== null && _a !== void 0 ? _a : 0,
                inBasket: true,
            });
        });
        basketView.render(view);
        const total = view.reduce((s, v) => s + v.price, 0);
        basketView.setTotal(total);
    });
    // подписка, чтобы product:add -> модель корзины добавляла товар
    bus.on('product:add', ({ id }) => {
        basketModel.add(id);
    });
    bus.on('product:remove', ({ id }) => {
        basketModel.remove(id);
    });
    // примеры: можно подписаться на оформление заказа (order:submit)
    bus.on('order:submit', async ({ order }) => {
        try {
            const resp = await apiClient.createOrder(order);
            bus.emit('order:success', { id: resp.id, total: resp.total });
            basketModel.clear();
        }
        catch (err) {
            bus.emit('order:error', { error: err instanceof Error ? err : new Error(String(err)) });
        }
    });
    // Загрузим продукты (шаг 2)
    try {
        await productModel.load(); // это вызовет products:loaded
    }
    catch (err) {
        console.error('Не удалось загрузить продукты', err);
    }
}
document.addEventListener('DOMContentLoaded', () => {
    bootstrap().catch(console.error);
});
