// src/views/CardView.ts
import { Component } from '../base/BaseComponent';
export class CardView extends Component {
    constructor(container, product, inBasket, eventBus) {
        super(container);
        this.product = product;
        this.inBasket = inBasket;
        this.eventBus = eventBus;
    }
    render() {
        const root = document.createElement('div');
        root.className = 'card';
        root.innerHTML = `
      <img src="${this.product.image}" alt="${this.product.title}" class="card__img" />
      <h3 class="card__title">${this.product.title}</h3>
      <p class="card__price">${this.product.price.toFixed(2)}</p>
    `;
        const btn = document.createElement('button');
        btn.textContent = this.inBasket ? 'В корзине' : 'Купить';
        btn.disabled = this.inBasket;
        btn.className = 'card__btn';
        btn.addEventListener('click', () => {
            if (!this.inBasket) {
                this.eventBus.emit('product:add', { id: this.product.id });
                // оптимистично меняем состояние UI
                btn.textContent = 'В корзине';
                btn.disabled = true;
                this.inBasket = true;
            }
            else {
                // редкая ситуация: убрать из корзины (если поддерживаем)
                this.eventBus.emit('product:remove', { id: this.product.id });
                btn.textContent = 'Купить';
                btn.disabled = false;
                this.inBasket = false;
            }
        });
        root.appendChild(btn);
        this.root = root;
        this.container.appendChild(root);
        return root;
    }
    update(inBasket) {
        this.inBasket = inBasket;
        if (!this.root)
            return;
        const btn = this.root.querySelector('button');
        if (btn) {
            btn.textContent = inBasket ? 'В корзине' : 'Купить';
            btn.disabled = inBasket;
        }
    }
}
