export class BasketView {
    constructor(container) {
        this.container = container;
        this.container.innerHTML = `
      <div class="basket__header"><h3>Корзина</h3></div>
      <div class="basket__list"></div>
      <div class="basket__footer">
        <div class="basket__total">Итого: <span class="basket__total-val">0</span></div>
      </div>
    `;
        this.listEl = this.container.querySelector('.basket__list');
        this.totalEl = this.container.querySelector('.basket__total-val'); // исправлен селектор
        this.checkoutBtn = document.createElement('button');
        this.checkoutBtn.textContent = 'Оформить';
        this.checkoutBtn.disabled = true;
        this.checkoutBtn.className = 'basket__checkout';
        this.container.querySelector('.basket__footer').appendChild(this.checkoutBtn);
    }
    render(items) {
        this.listEl.innerHTML = '';
        items.forEach((p) => {
            const row = document.createElement('div');
            row.className = 'basket-item';
            row.innerHTML = `<span>${p.title}</span> <span>${p.price.toFixed(2)}</span>`; // исправлены кавычки
            this.listEl.appendChild(row);
        });
    }
    setTotal(total) {
        this.totalEl.textContent = total.toFixed(2);
        this.toggleButton(total > 0);
    }
    toggleButton(enabled) {
        this.checkoutBtn.disabled = !enabled;
    }
}
