export class ProductModel {
    /**
     * @param api - экземпляр клиента, реализующего IApiClient
     * @param eventBus - EventBus для уведомлений
     */
    constructor(api, eventBus) {
        this.products = [];
        this.api = api;
        this.eventBus = eventBus;
    }
    async load() {
        const items = await this.api.getProducts();
        this.products = items;
        this.eventBus.emit('products:loaded', { items });
    }
    getProductById(id) {
        return this.products.find((p) => p.id === id);
    }
}
