export class ApiClient {
    constructor(api) {
        this.api = api;
    }
    async getProducts() {
        // предполагаем endpoint /products
        return this.api.get('/products');
    }
    async getProduct(id) {
        return this.api.get(`/products/${encodeURIComponent(id)}`);
    }
    async createOrder(order) {
        return this.api.post('/orders', order, 'POST');
    }
}
